@import url(https://pagecdn.io/lib/normalize/8.0.1/normalize.min.css);
:root{
 --clr-bg: #333333;
 --clr-muted: #444444;
 --clr-medium: #777777;
 --clr-bright: #ffffff;
 --clr-text: #ffffff;
 background-color: #333333;
 background-color: var(--clr-bg);
}
#root{
  margin: 15px;
}
.stack__disk{
  cursor: pointer;
  color: var(--clr-text);
}
.stack__disk:hover{
text-shadow: rgb(255, 255, 255) 1px 0 10px;
}
.stack__disk--active{
text-shadow: rgb(255, 255, 255) 1px 0 5px;
}
svg{
  height: 200;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  font-size:2rem;
}
.contact {
  text-align: center;
  grid-area: foot;


}

.contact > * {
  color: var(--clr-text);
}
.description {
  grid-area: desc;
  padding: .5rem;
  background-color: var(--clr-bg);
  border:solid 2px var(--clr-bright);
  /* box-shadow: -1px 2px 5px black; */
}

.header {
  display: grid;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  grid-template: 
    "img " auto
    "card" auto 
    /auto auto;
}
.header__card {
  grid-area: card;
  padding:0 1vw;
  background-color: var(--clr-bg);
  border:solid 2px var(--clr-bright);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  position: relative;
  bottom: 130%;
  /* box-shadow: -1px 2px 5px black; */
}
.header__img {
  grid-area: img;
  border-radius:50%;
  border: solid 2px var(--clr-bright);
  width: 120%;
  /* box-shadow: -1px 2px 5px black; */
}
.header__title {
  font-size: calc(2vmin);
  line-break: strict;
  line-height: .7;
}
.header__subtitle {
  line-height: 0.7;
  line-break: strict;
  font-size: calc(1.2vmin);

}
.stack {
  grid-area: stack;
  margin: 0 auto;
}
.stack__headline {
  font-family: 'Londrina Outline', cursive;
  font-size: 3rem;
  font-weight: 100;
  text-transform: uppercase;
  text-align: center;
  line-height: 2rem;
  margin-top: 0;
  margin-bottom: 1rem;
}
li {
  list-style-type: none;
}

.App {
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
  font-size: 16px;
  background-color: var(--clr-bg);
  color: var(--clr-text);
  margin: 5rem auto;
  max-width: 900px;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template: 
    "head desc stack" auto
    "foot foot foot" auto 
    /auto 33% auto;
}



